'use client';

import * as React from 'react';
import { Loader2 } from 'lucide-react';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { InputOTP, InputOTPGroup, InputOTPSlot } from '@/components/ui/input-otp';
import { toast } from 'sonner';
import { authClient } from '@/lib/auth-client';

// Type definitions for verification
type VerificationMethod = 'email' | 'sms';
type VerificationType = 'sign-in' | 'email-verification' | 'forget-password';

export interface OTPInputDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  identifier: string; // email or phone
  method: VerificationMethod;
  type: VerificationType;
  onSuccess: (otp?: string) => void;
}

/**
 * OTP Input Dialog Component
 *
 * Allows users to enter and verify OTP codes
 */
export function OTPInputDialog({
  open,
  onOpenChange,
  identifier,
  method,
  type,
  onSuccess,
}: OTPInputDialogProps) {
  const [otp, setOtp] = React.useState('');
  const [isVerifying, setIsVerifying] = React.useState(false);
  const [isResending, setIsResending] = React.useState(false);
  const [countdown, setCountdown] = React.useState(0);

  // Countdown timer for resend button
  React.useEffect(() => {
    if (countdown > 0) {
      const timer = setTimeout(() => setCountdown(countdown - 1), 1000);
      return () => clearTimeout(timer);
    }
  }, [countdown]);

  // Auto-submit when OTP is complete
  React.useEffect(() => {
    if (otp.length === 6 && !isVerifying) {
      handleVerify();
    }
  }, [otp]);

  // Reset OTP when dialog opens
  React.useEffect(() => {
    if (open) {
      setOtp('');
      setCountdown(60); // Start 60s countdown
    }
  }, [open]);

  const handleVerify = async () => {
    if (otp.length !== 6) return;

    setIsVerifying(true);

    try {
      console.log('üîç OTP Í≤ÄÏ¶ù ÏãúÏûë:', { method, type, identifier, otp });

      // For forget-password (both email and SMS), just pass OTP to parent
      // Parent will call better-auth's resetPassword with the OTP
      if (type === 'forget-password') {
        console.log('üîë ÎπÑÎ∞ÄÎ≤àÌò∏ Ïû¨ÏÑ§Ï†ï: OTPÎ•º Î∂ÄÎ™®Î°ú Ï†ÑÎã¨');
        toast.success('Ïù∏Ï¶ùÏΩîÎìúÎ•º ÌôïÏù∏ÌñàÏäµÎãàÎã§');
        onSuccess(otp); // Pass OTP to parent for better-auth resetPassword
        onOpenChange(false);
        return;
      }

      // For email signup verification, use custom API (doesn't require existing user)
      if (method === 'email' && type === 'email-verification') {
        console.log('üìß Ïù¥Î©îÏùº ÌöåÏõêÍ∞ÄÏûÖ Í≤ÄÏ¶ù Ï§ë...');

        const response = await fetch('/api/auth/verify-email-otp', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            email: identifier,
            code: otp,
          } satisfies import('@/app/api/auth/verify-email-otp/route').VerifyEmailOtpRequest),
        });

        const result: import('@/app/api/auth/verify-email-otp/route').VerifyEmailOtpResponse | import('@/app/api/auth/verify-email-otp/route').VerifyEmailOtpErrorResponse = await response.json();

        if (!response.ok) {
          console.error('‚ùå Ïù¥Î©îÏùº OTP Í≤ÄÏ¶ù Ïã§Ìå®:', result);
          toast.error('error' in result ? result.error : 'Ïù∏Ï¶ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§');
          setOtp(''); // Clear for retry
          return;
        }
      } else if (method === 'sms') {
        // SMS verification: Use custom API (doesn't require existing user)
        console.log('üì± SMS OTP Í≤ÄÏ¶ù Ï§ë...');

        const response = await fetch('/api/auth/verify-phone-otp', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            phoneNumber: identifier,
            code: otp,
          } satisfies import('@/app/api/auth/verify-phone-otp/route').VerifyPhoneOtpRequest),
        });

        const result: import('@/app/api/auth/verify-phone-otp/route').VerifyPhoneOtpResponse | import('@/app/api/auth/verify-phone-otp/route').VerifyPhoneOtpErrorResponse = await response.json();

        if (!response.ok) {
          console.error('‚ùå SMS OTP Í≤ÄÏ¶ù Ïã§Ìå®:', result);
          toast.error('error' in result ? result.error : 'Ïù∏Ï¶ùÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§');
          setOtp(''); // Clear for retry
          return;
        }
      }

      toast.success('Ïù∏Ï¶ùÏù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§');
      onSuccess(otp); // Pass the OTP back to parent
      onOpenChange(false);
    } catch (error) {
      console.error('Verification error:', error);
      toast.error('Ïù∏Ï¶ù Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§');
      setOtp('');
    } finally {
      setIsVerifying(false);
    }
  };

  const handleResend = async () => {
    setIsResending(true);

    try {
      if (method === 'email') {
        // Ïù¥Î©îÏùº Ïû¨Ï†ÑÏÜ°: better-auth emailOtp ÌîåÎü¨Í∑∏Ïù∏ ÏÇ¨Ïö©
        console.log('üìß Ïù¥Î©îÏùº OTP Ïû¨Ï†ÑÏÜ° Ï§ë...');
        await authClient.emailOtp.sendVerificationOtp({
          email: identifier,
          type,
        });
        console.log('‚úÖ Ïù¥Î©îÏùº OTP Ïû¨Ï†ÑÏÜ° ÏôÑÎ£å');
      } else {
        // SMS Ïû¨Ï†ÑÏÜ°: better-auth phoneNumber ÌîåÎü¨Í∑∏Ïù∏ ÏÇ¨Ïö©
        console.log('üì± SMS OTP Ïû¨Ï†ÑÏÜ° Ï§ë...');
        await authClient.phoneNumber.sendOtp({
          phoneNumber: identifier,
        });
        console.log('‚úÖ SMS OTP Ïû¨Ï†ÑÏÜ° ÏôÑÎ£å');
      }

      toast.success('Ïù∏Ï¶ùÏΩîÎìúÍ∞Ä Ïû¨Ï†ÑÏÜ°ÎêòÏóàÏäµÎãàÎã§');
      setCountdown(60); // Restart countdown
      setOtp(''); // Clear current input
    } catch (error) {
      console.error('Resend error:', error);
      toast.error('Ïû¨Ï†ÑÏÜ° Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§');
    } finally {
      setIsResending(false);
    }
  };

  const getTitle = () => {
    switch (type) {
      case 'sign-in':
        return 'Î°úÍ∑∏Ïù∏ Ïù∏Ï¶ù';
      case 'email-verification':
        return 'Ïù¥Î©îÏùº Ïù∏Ï¶ù';
      case 'forget-password':
        return 'ÎπÑÎ∞ÄÎ≤àÌò∏ Ïû¨ÏÑ§Ï†ï Ïù∏Ï¶ù';
      default:
        return 'Ïù∏Ï¶ù';
    }
  };

  const getDescription = () => {
    const destination = method === 'email' ? identifier : identifier;
    return `${destination}ÏúºÎ°ú Ï†ÑÏÜ°Îêú 6ÏûêÎ¶¨ Ïù∏Ï¶ùÏΩîÎìúÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.`;
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle>{getTitle()}</DialogTitle>
          <DialogDescription>{getDescription()}</DialogDescription>
        </DialogHeader>

        <div className="space-y-6 py-4">
          {/* OTP Input */}
          <div className="flex justify-center">
            <InputOTP maxLength={6} value={otp} onChange={setOtp} disabled={isVerifying}>
              <InputOTPGroup>
                <InputOTPSlot index={0} />
                <InputOTPSlot index={1} />
                <InputOTPSlot index={2} />
                <InputOTPSlot index={3} />
                <InputOTPSlot index={4} />
                <InputOTPSlot index={5} />
              </InputOTPGroup>
            </InputOTP>
          </div>

          {/* Info & Actions */}
          <div className="space-y-3">
            <p className="text-muted-foreground text-center text-sm">
              {otp.length === 6
                ? 'Ïù∏Ï¶ù Ï§ëÏûÖÎãàÎã§...'
                : `Ïù∏Ï¶ùÏΩîÎìú ${otp.length}/6 ÏûÖÎ†• (10Î∂ÑÍ∞Ñ Ïú†Ìö®)`}
            </p>

            <div className="flex gap-2">
              <Button
                type="button"
                variant="outline"
                size="sm"
                onClick={handleResend}
                disabled={countdown > 0 || isResending || isVerifying}
                className="flex-1"
              >
                {isResending ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    Ïû¨Ï†ÑÏÜ° Ï§ë...
                  </>
                ) : countdown > 0 ? (
                  `Ïû¨Ï†ÑÏÜ° (${countdown}Ï¥à)`
                ) : (
                  'Ïû¨Ï†ÑÏÜ°'
                )}
              </Button>

              <Button
                type="button"
                onClick={handleVerify}
                disabled={otp.length !== 6 || isVerifying}
                className="flex-1"
              >
                {isVerifying ? (
                  <>
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    ÌôïÏù∏ Ï§ë...
                  </>
                ) : (
                  'ÌôïÏù∏'
                )}
              </Button>
            </div>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  );
}
