/**
 * Password strength calculation
 */
export function calculatePasswordStrength(password: string): {
  score: number;
  level: 'weak' | 'medium' | 'strong' | 'very-strong';
  feedback: string[];
} {
  let score = 0;
  const feedback: string[] = [];

  // Length check
  if (password.length >= 8) score += 1;
  if (password.length >= 12) score += 1;
  if (password.length >= 16) score += 1;

  // Character variety
  if (/[a-z]/.test(password)) score += 1;
  if (/[A-Z]/.test(password)) score += 1;
  if (/\d/.test(password)) score += 1;
  if (/[!@#$%^&*()_+\-=\[\]{};':"\\|,.<>\/?~`]/.test(password)) score += 1;

  // Avoid common patterns
  if (!COMMON_PASSWORDS.includes(password.toLowerCase())) score += 1;
  if (!KOREAN_COMMON_PASSWORDS.includes(password.toLowerCase())) score += 1;
  if (!SEQUENTIAL_PATTERNS.some((pattern) => pattern.test(password.toLowerCase()))) score += 1;

  // Determine level and feedback
  if (score <= 3) {
    feedback.push('Îçî Í∞ïÎ†•Ìïú ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÏÑ§Ï†ïÌïòÏÑ∏Ïöî');
    feedback.push('ÏµúÏÜå 8Ïûê Ïù¥ÏÉÅ ÏÇ¨Ïö©ÌïòÏÑ∏Ïöî');
    feedback.push('ÎåÄÎ¨∏Ïûê, ÏÜåÎ¨∏Ïûê, Ïà´Ïûê, ÌäπÏàòÎ¨∏ÏûêÎ•º Î™®Îëê Ìè¨Ìï®ÌïòÏÑ∏Ïöî');
    return { score, level: 'weak', feedback };
  } else if (score <= 5) {
    feedback.push('ÎπÑÎ∞ÄÎ≤àÌò∏Í∞Ä Î≥¥ÌÜµ ÏàòÏ§ÄÏûÖÎãàÎã§');
    feedback.push('12Ïûê Ïù¥ÏÉÅ ÏÇ¨Ïö©ÌïòÎ©¥ Îçî ÏïàÏ†ÑÌï©ÎãàÎã§');
    return { score, level: 'medium', feedback };
  } else if (score <= 7) {
    feedback.push('Í∞ïÎ†•Ìïú ÎπÑÎ∞ÄÎ≤àÌò∏ÏûÖÎãàÎã§');
    return { score, level: 'strong', feedback };
  } else {
    feedback.push('Îß§Ïö∞ Í∞ïÎ†•Ìïú ÎπÑÎ∞ÄÎ≤àÌò∏ÏûÖÎãàÎã§');
    return { score, level: 'very-strong', feedback };
  }
}

/**
 * Check if password contains personal information
 */
export function checkPersonalInfo(
  password: string,
  userInfo: {
    name?: string;
    email?: string;
    phone?: string;
  }
): string[] {
  const warnings: string[] = [];
  const lowerPassword = password.toLowerCase();

  if (userInfo.name && lowerPassword.includes(userInfo.name.toLowerCase())) {
    warnings.push('ÎπÑÎ∞ÄÎ≤àÌò∏Ïóê Ïù¥Î¶ÑÏùÑ Ìè¨Ìï®ÌïòÏßÄ ÎßàÏÑ∏Ïöî');
  }

  if (userInfo.email) {
    const emailParts = userInfo.email.toLowerCase().split('@')[0];
    if (lowerPassword.includes(emailParts)) {
      warnings.push('ÎπÑÎ∞ÄÎ≤àÌò∏Ïóê Ïù¥Î©îÏùº Ï£ºÏÜåÎ•º Ìè¨Ìï®ÌïòÏßÄ ÎßàÏÑ∏Ïöî');
    }
  }

  if (userInfo.phone) {
    const phoneNumbers = userInfo.phone.replace(/\D/g, '');
    if (phoneNumbers.length >= 4 && lowerPassword.includes(phoneNumbers.slice(-4))) {
      warnings.push('ÎπÑÎ∞ÄÎ≤àÌò∏Ïóê Ï†ÑÌôîÎ≤àÌò∏Î•º Ìè¨Ìï®ÌïòÏßÄ ÎßàÏÑ∏Ïöî');
    }
  }

  return warnings;
}

